.wao-topbar {
	width: 100%;
	display: flex;
	@media screen and (min-width: 800px) {
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		height: rem(100px);
		box-shadow: inset 0 -2px 0 0  #f2f3f4;
		transition: margin-top .3s ease-in-out;
	}
	@media (max-width: 799.98px) {
		flex-direction: column;
		position: relative;
	}
	&__logo {
		a {
			display: block;
		}
		img {
			height: rem(70px);
			width: rem(200px);
		}
		@media (max-width: 799.98px) {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			img {
				height: rem(45px);
				width: rem(130px);
			}
		}
		&--scroll {
			display: none;
			@media (min-width: 800px) {
				// display: block;
				max-width: 12rem;
				// margin-right: auto;
				// transform: scale(0);
				// transition: transform .3s ease-in-out;
			}
		}
	}
	&__search {
		// background-color: #f2f3f4;
		// width: 200px;
		// height: 44px;
		// display: flex;
		// align-items: center;
		// padding: rem(12px);
		
		input {
			border: none;
			// font-size: rem(14px);
			font-size: rem(14px);
			line-height: 1.43;
			width: 100%;
			background-color: inherit;
		}
		button {
			background-color: inherit;
			border: none;
			cursor: pointer;
			font-size: rem(20px);
			padding: 0;
		}
		@media (min-width: 800px) {
			background-color: #f2f3f4;
			width: 200px;
			height: 44px;
			display: flex;
			align-items: center;
			padding: rem(12px)
		}
		@media (min-width: 1200px) {
			width: 353px;
		}
		@media (max-width: 799.98px) {
			input {
				position: absolute;
				top: 100%;
				right: 0;
				background-color: #f2f3f4;
				width: 200px;
				height: 44px;
				padding: .5rem;
				transform: translateY(10px);
				opacity: 0;
				visibility: hidden;
				transition: all .4s ease-in-out;
			}
		}
		&.active {
			input {
				transform: translateY(0);
				opacity: 1;
				visibility: visible;
			}
		}
	}
	&__right {
		display: flex;
		align-items: center;
		position: relative;
		@media (max-width: 799.98px) {
			display: none;
		}
	}
	&__left {
		@media (max-width: 799.98px) {
			position: relative;
			padding: rem(10px) 0;
			height: rem(70px);
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}
	&__toggle {
		display: none;
		@media (max-width: 799.98px) {
			display: block;
			font-size: rem(20px);
		}
	}
	&__notification, &__message {
		font-size: 2rem;
	}
	&__tools {
		display: flex;
		align-items: center;
		.wao-topbar__message {
			display: inline-block;
		}
		.wao-topbar__notification, .wao-topbar__message{
			margin-right: 2rem;
			cursor: pointer;
			&--active {
				color: #ff6060;
			}
		}
		svg {
			fill: #081526;
			transition: fill .3s ease-in-out;
			&:hover {
				fill: #ff6060;
			}
		}
		@media (max-width: 799.98px) {
			margin-bottom: 2rem;
		}
	}
	&__social {
		margin-right: rem(16px);
		a {
			display: inline-block;
			width: rem(35px);
			height: rem(35px);
			text-align: center;
			border-radius: 50%;
			border: solid 1px #081526;
			line-height: rem(35px);
			font-size: rem(20px);
			&:not(:last-child){
				margin-right: rem(11px);
			}
		}
		@media (min-width: 800px) {
			margin-right: 2.6rem;
		}
		@media (max-width: 799.98px) {
			margin-bottom: 2rem;
		}
	}
	&__btn {
		&-register, &-login{
			display: inline-block;
			font-size: rem(16px);
			font-weight: 700;
			border-radius: 18px;
			height: rem(36px);
			line-height: rem(36px);
			text-align: center;
			cursor: pointer;
		}
		&-user {
			p {
				display: none;
			}
			@media (max-width: 799.98px) {
				&--active {
					color: #ff6060;
				}
			}
			
		}
		&-register {
			width: rem(113px);
			height: rem(36px);
			border: solid 1px #081526;
			color: #081526;
			margin-right: rem(16px);
		}
		&-login {
			width: rem(137px);
			background-color: #ff6060;
			color: #ffffff!important;
			svg {
				fill: #ffffff;
				margin-right: rem(8px);
			}
			@include media("<xs"){
				width: rem(113px);
				svg {
					display: none;
				}
			}
		}
	}

	&__icons {
		display: none;
		@media (max-width: 799.98px) {
			display: flex;
			align-items: center;
			justify-content: space-around;
			box-shadow: inset 0 1px 0 #f2f3f4;
			position: relative;
			.wao-topbar__btn-user, .wao-topbar__message, .wao-topbar__notification, .wao-topbar__search, .wao-topbar__toggle {
				width: 100%;
				text-align: center;
				padding: 1rem;
			}
			.wao-topbar__btn-user, .wao-topbar__notification {
				// position: relative;
			}
		}
	}
	&__dropdown {
		display: block;
		width: 100%;
		right: 0;
		box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.08), 0 2px 12px 0 rgba(0, 0, 0, 0.12), 0 2px 16px 0 rgba(0, 0, 0, 0.16);
		border: none;
		border-radius: 8px;
		// padding: 0 2rem;
		margin-top: 0;
		margin-top: 1.2rem;
		position: absolute;
		top: 100%;
		background-color: #fff;
		white-space: nowrap;
		z-index: 50;
		opacity: 0;
		visibility: hidden;
		transform: translateY(10px);
		transition: all .3s ease-in-out;
		@media (min-width: 800px) {
			&:before {
				content: '';
				position: absolute;
				top: -10px;
				right: 24px;
				display: inline-block;
				width: 0;
				height: 0;
				border-style: solid;
				border: 1rem solid black;
				border-color: transparent transparent #fff #fff;
				box-shadow: -2px 4px 3px 0 rgba(0, 0, 0, 0.1);
				transform: rotate(135deg);
			}
		}
		&-account-item {
			text-align: left;
			padding: 1.6rem 0 1.5rem 0;
			border-bottom: 1px solid #cccccc;
			text-transform: uppercase;
			font-size: 1.4rem;
			font-weight: 700;
			display: flex;
			align-items: baseline;
			color: #081526;
			transition: color .3s ease-in-out;
			i {
				font-size: 2rem;
				margin-right: 1.2rem;
			}
			&:hover, &--active {
				color: #ffa34d;
				background-color: inherit;
			}
			&:last-child {
				border-bottom: 0;
			}
			span {
				text-transform: initial;
				width: 100%;
				font-weight: 700;
				text-align: right;
			}
		}
		&-toggle-account {
			i {
				font-size: 2rem;
			}
			@media (min-width: 800px) {
				padding: 0 3.2rem 0 1.6rem;
				height: 3.6rem;
				width: 16.8rem;
				border-radius: 18px;
				overflow: hidden;
				border: 1px solid #081526;
				cursor: pointer;
				position: relative;
				display: flex;
				align-items: center;
				&::before {
					content: '\f078';
					font-family: 'FontAwesome';
					font-size: .9rem;
					position: absolute;
					top: 50%;
					right: 16px;
					transform: translateY(-50%);
				}
				i {
					margin-right: .8rem;
				}
				p {
					display: inline-block;
					font-size: 1.6rem;
					font-weight: 700;
					text-transform: uppercase;
					overflow: hidden;
					text-overflow: ellipsis;
					-webkit-line-clamp: 1;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					max-height: 2rem;
					margin-bottom: 0;
					white-space: nowrap;
				}
			}
		}
		&-account {
			padding: 0 2rem;
			@media (min-width: 800px) {
				width: 26.8rem;
				
			}
		}
		&-notification {
			@media (min-width: 800px) {
				width: 36.8rem;
				&:before {
					right: 188px;
				}
			}
			&-body {
				max-height: 40rem;
				overflow-y: auto;
			}
			&-header {
				padding: 1.6rem 1.6rem 1.1rem 1.6rem;
				display: flex;
				align-items: center;
				justify-content: space-between;
				border-bottom: 1px solid #cccccc;
				h4 {
					margin-bottom: 0;
					font-size: 1.4rem;
					font-weight: 700;
				}
				a {
					font-size: 1.2rem;
					text-decoration: underline;
					font-weight: 400;
				}
			}
			&-item {
				display: flex;
				padding: 1.2rem 1.6rem 1.5rem 1.6rem;
				white-space: normal;
				border-bottom: 1px solid #cccccc;
				img {
					height: 3.6rem;
					width: 3.6rem;
					object-fit: cover;
					margin-right: 1.2rem;
				}
				h5 {
					font-size: 1.4rem;
					font-weight: 700;
					margin-bottom: .7rem;
					span {
						font-weight: 400;
						display: inline-block;
						margin-left: 1.2rem;
					}
				}
				p {
					font-size: 1.2rem;
					line-height: 1.33;
					font-weight: 400;
					margin-bottom: 0;
				}
				&--unSeen {
					background-color: rgba(255, 140, 102, 0.1)
				}
			}
			&-dicription {
				text-align: left;
			}
			
		}
		&--show {
			opacity: 1;
			visibility: visible;
			transform: translateY(0);
		}
	}
	.wao-topbar__dropdown-toggle-notification {
		&--active {
			color: #ff6060;
		}
	}
}
