.home-users {
	// overflow: hidden;
	margin-bottom: 2.4rem;
	.container-small {
		@media (min-width: 1200px) {
			padding-right: 9.8rem;
			padding-left: 9.8rem;
		}
	}
}
.home-users {
	@media (min-width: 992px) {
		.block-search {
			margin-top: rem(-60px);
		}
	}
	@media (max-width: 992.98px) {
		padding-top: 2.4rem;
	}
}
.home-users .lists-user{
	@media (min-width: 992px) {
		margin-top: -6rem;
	}
	&__title{
		height: rem(60px);
		background-color: white;
		@media (min-width: 768px) {
			border-top-right-radius: .8rem;
		}
		padding: 2rem;
		border-bottom:1px solid rgba(8, 21, 38, 0.2);
		label{
			align-items: center;
			display: flex;
			font-size: 2rem;
			font-weight: 700;
			// border-bottom:1px solid rgba(8, 21, 38, 0.2);
			text-transform: uppercase;
			width: 100%;
			height: 100%;
		}
	}
	.nav-tabs{
		border:none;
		background-color: white;
		@media (min-width: 992px) {
			border-top-left-radius: 8px;
			border-top-right-radius: 8px;
			overflow: hidden;
		}
		li{
			width: 50%;
			a{
				font-size: 2rem;
				color:#081526;
				height: 6.4rem;
				display: flex;
				justify-content: center;
				align-items: center;
				background-color: #f2f3f4;
				opacity: 0.5;
				font-weight: 700;
				// text-transform: uppercase;
				&.active{
					opacity: 1;
					background-color: white;
					border-bottom:1px solid rgba(8, 21, 38, 0.2)
				}
			}
		}
		
	}
	@include media("<xs") {
		margin-top: 0;
	}
}
.blog-home{
	// padding: 3.2rem 0;
	padding-top: 2.4rem;
	.post-group{
		padding: 2.4rem 2.4rem;
		padding-bottom: 0;
		background-color: white;
		// border-radius: 0.8rem;
		&__header{
			display: flex;
			justify-content: space-between;
			padding-bottom: 2.4rem;
			border-bottom:1px solid rgba(8, 21, 38, 0.2);
			margin-bottom: 2.4rem;
			h2{
				font-size: 2rem;
				font-weight: 700;
				// text-transform: uppercase;
				margin-bottom: 0;
			}
			a{
				font-size: 1.4rem;
				color: $color-1st;
				// text-transform: uppercase;
				text-decoration: underline;
			}
		}
		.card-middle{
			color:rgba(8, 21, 38, 0.3);
		}
	}
	@include media("<xs") {
		// padding-top: 0;
		.col-md-6{
			padding: 0;
		}
		.post-group{
			border-radius: 0;
		}
	}
}

.featured-concepts{
	&__content{
		display: flex;
		// flex-wrap: wrap;
		flex-direction: column;
		margin-bottom: 2.4rem;
		// border-radius: 8px;
		// overflow: hidden;
		@include media(">md"){
			flex-direction: row;
			position: relative;
			&::before{
				content: '';
				height: 100%;
				width: 31.5%;
				background-color: #f2f3f4;
				position: absolute;
				top: 0;
				left: 0;
			}
		}
	}
	&__left{
		padding-left: 3.6rem;
		padding-top: 16.6rem;
		@include media(">md"){
			width: 23%;
		}
		h2{
			line-height: 1.2;
		}
		.btn-featured{
			padding: 1rem 4.8rem;
			font-size: 1.4rem;
		}
		@include media("<xs") {
			width: 100%;
			padding-left: 2rem;
			padding-top: 3rem;
			padding-right: 2rem;
			h2{
				font-size: 2.8rem!important
			}
		}

	}
	&__desc{
		padding: 1.6rem 0 2rem 0;
		position: relative;
		font-size: 1.4rem;
		@include media("<xs") {
			padding-top: 1rem;
			padding-bottom: 2rem;
		}
	}
	&__right{
		padding-right: 4rem;
		// padding-bottom: 8.2rem;
		padding-left: 3.2rem;
		@include media("<xs") {
			// width: 100%;
			padding: 2rem;
		}
		@include media(">md"){
			width: 77%;
		}
	}
	&__title {
		h2 {
			font-size: 4rem;
			margin-bottom: 0;
		}
	}
	.sync2__nav {
		position: relative;
		width: 72px;
		height: 72px;
		// &::before{
		// 	content:'';
		// 	padding-top: 100%;
		// 	display: block
		// }
		img{
			// position: absolute;
			// top: 0;
			// left: 0;
			max-width: inherit;
			width: 72px;
			height: 72px;
			border-radius: 50%;
			object-fit: cover;
		}
	}
	
}
	  
#sync2{
	padding: 0 5.6rem 0 5.6rem;
	border-bottom: 2px solid #979797;
	position: relative;
	.owl-stage-outer{
		position: relative;
		z-index: 2;
		padding-top: 5.8rem;
		padding-bottom: 5.5rem;
	}
	
	.owl-nav{
		position: absolute;
		width: 100%;
		top:50%;
		left: 0;
		transform: translateY(-50%);
		margin: 0;
		display: flex;
		justify-content: space-between;
		.owl-prev, .owl-next{
			margin: 0;
			background: #89898a;
			border-radius: 100%;
			// width: 5.6rem;
			// height: 5.6rem;
			width: 3.2rem;
			height: 3.2rem;
			display: flex;
			justify-content: center;
			align-items: center;
		}
		
		.wd-icon{
			color: white;
			// font-size: 3.2rem;
			font-size: rem(18.3px);
			margin: 0;
		}
	}
	.sync2__name{
		// position: absolute;
		// bottom: -4rem;
		// text-align: center;
		// width: 100%;
		text-align: center;
		margin-top: 1.2rem;
		span {
			font-size: 1.2rem;
			font-weight: 700;
		}
	}
	.owl-item{
		padding: 0 rem(19.4px);
		display: flex;
		justify-content: center;
		// .sync2__nav:after{
		// 	transition: ease 0.5s;
		// 	position: absolute;
		// 	width: 100%;
		// 	height:0.8rem;
		// 	background-color:$color-1st;
		// 	display: block;
		// 	bottom: -6.7rem;
		// 	left: 0;
		// 	content: '';
		// 	opacity:0;
		// 	visibility: hidden;
		// }
		.sync2__nav img{
			transition: ease 0.5s;
		}
		&:hover, &.current{
			.sync2__nav{
				position: relative;
				img{
					transform: scale(1.2);
				}
				&:before {
					content: '';
					width: 100%;
					height: rem(4px);
					background-color: #ff6060;
					position: absolute;
					left: 0;
					bottom: -55px;
					z-index: 1;
				}
			}
			.sync2__nav:after{
				opacity: 1;
				visibility: visible;
			}
		}
		&.current {
			.sync2__nav {
				&:before {
					height: rem(8px);
				}
			}
		}
	}
}

